.banner{
    margin-top: 70px;
    background-size: cover;
    height: 400px;
    color: white;
    
}
.content{
    padding: 10px;
    height: 190px;
    padding-left: 20px;
}
.titlel{
    font-size: 4rem;
    font-weight: 800;
    padding-bottom: 0.3rem;

}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color:rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-left: 2rem;
  
}
.button:hover{
    color: black;
    background-color: #e6e6e6;

}
.discription{
    width: 45rem;
    line-height: 1.3;
    padding-top: 2rem;
    font-size: 1rem;
    height: 80px;
    max-width: 400px;
}
.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),rgb(0, 0, 0));
    margin-top: 93px;

}